.i{
    display: flex;
    height: 100vh;
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-rigth{
    flex: 1;
    position: relative;
    overflow: hidden;
}

.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 50px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move{
    50%{
        transform: translateY(-50px);
    }
    /* 100%{
        transform: translateY(-100px);
    } */
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #757575;
    display: flex;
    align-items: center;
}

.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}
.i-img{
    padding-top: 10px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.i-bg{
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: #757575;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    overflow: visible;
}

@media screen and (max-width: 480px){
    .i{
        flex-direction: column;
    }

    .i-left-wrapper{
       padding: 10px;
       align-items: center;
       justify-content: center;
       text-align: center;
       height: 100%;
    }

    .i-img{
        object-fit: contain;
        padding-bottom: 10px;
    }

    .i-desc{
        display: none;
    }
}