.a {
    height: 50vh;
    display: flex;
    align-items: center;
}

.a-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-rigth {
    flex: 1;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-skills{
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    margin-right: 40px;
    justify-content: center;
    list-style-type: none;
}

.a-card.bg {
    position: absolute;
    top: 200px;
    left: 50px;
    background-color: #333;
}

.a-card {
    width: 70%;
    height: 50vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 20px;
}

.a-sub{
    margin-right: 40px;
}

.i-desc{
    margin-right: 40px;
}

li {
    display: flex;
    align-items: center;
    padding: 0 10px;
    height: 30px;
    border-radius: 17px;
    border: 1px solid white;
}

@media screen and (max-width: 480px){
    .a{
        height: auto;
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }

    .a-left{
        width: 100%;
    }

    .a-card{
        height: 30vh;
    }

    .a-card.bg{
        display: none;
    }

    .a-rigth{
        padding: 20px;
    }

    .a-sub{
        margin-right: 0px;
    }
    
    .i-desc{
        margin-right: 0px;
    }

}