.t {
    width: 50px;
    height: 20px;
    border-radius: 20px;
    border: 1pz solid #999;
    background-color: white;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-right: 10px;
}

.t > svg {
    color: #000 !important;
}

.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}